                </el-form-item>
                <input type="hidden" name="welfareLabel" v-model="changeWelfareLabelStr">               <!-- 福利标签 -->               
                <el-form-item label="年龄要求" class="post-el-form">
                    <el-col class="position-salary position-low-salary">
                        <el-form-item prop="postLowAge">
                            <el-input placeholder="最小值" name="ageFrom" v-model.trim="postModel.postLowAge"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col class="position-salary position-high-salary">                       
                        <el-form-item prop="postHighAge">
                            <el-input placeholder="最大值" name="ageTo" v-model.trim="postModel.postHighAge"></el-input>
                        </el-form-item>
                    </el-col>
                </el-form-item>
                <el-form-item label="学历要求" class="post-el-form">
                    <span class="el-span" @click="chanceEducation">{{educationValue}}</span>
                    <i class="relate-icon"></i>
                </el-form-item>
                <input type="hidden" name="educationMin" v-model="changeEducationMin">                  <!-- 学历要求 -->
                <el-form-item label="性别要求" class="post-el-form">
                    <span class="el-span" @click="showGenderPopup">{{genderVal}}</span>
                    <i class="relate-icon"></i>
                </el-form-item>
                <input type="hidden" name="gender" v-model="changeGender">                       <!-- 性别要求 -->
