                <input type="hidden" name="workAddress" v-model="positionDetailsAddress">
                <input type="hidden" name="workProvinceCode" v-model="changeWorkProvinceCode">
                <input type="hidden" name="interviewProvinceCode" v-model="changeWorkProvinceCode">
                <input type="hidden" name="provinceCode" v-model="changeWorkProvinceCode">
                <input type="hidden" name="workCityCode" v-model="changeWorkCityCode">
                <input type="hidden" name="interviewCityCode" v-model="changeWorkCityCode">
                <input type="hidden" name="cityCode" v-model="changeWorkCityCode">
                <input type="hidden" name="workDistCode" v-model="changeWorkDistCode">
                <input type="hidden" name="interviewDistCode" v-model="changeWorkDistCode">
                <input type="hidden" name="countyCode" v-model="changeWorkDistCode">
                <!-- <el-form-item label="详细地址" class="post-el-form">
                    <el-input class="post-el-input" placeholder="请输入详细地址" v-model="postModel.postPerson"></el-input>
                </el-form-item> -->
                <el-form-item label="咨询电话" class="post-el-form">
                    <el-input class="post-el-input"  name="consultTel" placeholder="请输入咨询电话" v-model="postModel.consultTel"></el-input>
                </el-form-item>
                <el-form-item label="工作内容" class="post-el-form">
                    <span class="el-span el-textarea-span" @click="qdsWorkContent">{{workContentVal}}</span>
                    <i class="relate-icon"></i>
                </el-form-item>
                <input type="hidden" name="jobContent" v-model="jobContent">
