                    <div class="post-fm-img">
                        <img :src="postModel.imgBusinessLicence" alt="" @click="bigImgBusinessLicence">
                    </div>
                    <input type="file" id="logoImg" class="post-fm-im-hidden" accept="image/*" @change="uploadImg($event, 1)">
                    <label for="logoImg" class="upload-post-fm-img" v-show="toggleUploadBtn"></label>
                    <input type="hidden" name="coverImgUrl" v-model="coverImgUrl">
                </el-form-item>
            </el-form>
        </div>
        <div class="zhanwei"></div>
        <div class="release-foot">
            <el-button type="primary" class="release-btn release-save" @click="savePostForm">保存</el-button>
            <el-button type="primary" class="release-btn release-save-release" @click="saveAndRelease">保存并上架</el-button>
        </div>
