                </div>
                <div class="left-content" v-if="positionTypeArrTwo">
                    <p :class="{headerItemActive:selectTwoCode == item.value}" v-for="(item,index) in positionTypeArrTwo" @click="postTypeSelectTwo(item.children,item.value,item.label)" :key="index">{{item.label}}</p>
                </div>
                <div class="left-content" v-if="positionTypeArrThree">
