    2.按工艺要求进行生产操作；
    3.服从领导安排，完成本岗以外的技术学习任务；
    4.完成领导交办的临时工作。"
                    v-model="workContentValTextarea">
                </el-input>
            </div>
            <a href="javascript:;" class="sure" @click="sureWorkContent">完  成</a>
        </div>
        <!-- 百度地图 -->
