                    <el-input class="company-auth-input-width" v-model="showCityAddress" type="hidden" ></el-input>
                </div>
                <baidu-map class="map-body" :zoom="15" :scroll-wheel-zoom="true" :center="{lng: 121.4853300000, lat: 31.3342000000}" >
                    <bm-view class="map"></bm-view>
                    <bm-local-search :keyword="showCityAddress" :auto-viewport="true" ></bm-local-search>
                    <bm-overview-map anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :isOpen="true"></bm-overview-map>         <!-- 缩略图 -->
                    <bm-navigation class="map-zoom" anchor="BMAP_ANCHOR_TOP_LEFT"></bm-navigation>     <!-- 缩放 -->
                </baidu-map>
            </div>
        </div>
        <!-- 图片裁剪上传 -->
        <div class="welfare-popup cut-img-box" v-show="showCutImg">
            <div class="welfare-title">
                <div class="van-picker__cancel" @click="falseCutImg">取消</div>
                <div class="van-picker__confirm" @click="trueCutImg">确认</div>
            </div>
            <!-- img	裁剪图片的地址	空	url 地址 || base64 || blob
                outputSize	裁剪生成图片的质量	1	0.1 - 1
                outputType	裁剪生成图片的格式	jpg (jpg 需要传入jpeg)	jpeg || png || webp
                info	裁剪框的大小信息	true	true || false
                canScale	图片是否允许滚轮缩放	true	true || false
                autoCrop	是否默认生成截图框	false	true || false
                autoCropWidth	默认生成截图框宽度	容器的80%	0~max
                autoCropHeight	默认生成截图框高度	容器的80%	0~max
                fixed	是否开启截图框宽高固定比例	true	true | false
                fixedNumber	截图框的宽高比例	[1 : 1]	[宽度 : 高度]
                full	是否输出原图比例的截图	false	true | false
                fixedBox	固定截图框大小 不允许改变	false	true | false
                canMove	上传图片是否可以移动	true	true | false
                canMoveBox	截图框能否拖动	true	true | false
                original	上传图片按照原始比例渲染	false	true | false
                centerBox	截图框是否被限制在图片里面	false	true | false
                high	是否按照设备的dpr 输出等比例图片	true	true | false
                infoTrue	true 为展示真实输出图片宽高 false 展示看到的截图框宽高	false	true | false
                maxImgSize	限制图片最大宽度和高度	2000	0-max -->
            <div class="cropper">
                <vue-cropper 
                    ref="cropper" 
                    :img="option.img" 
                    :outputSize="option.size" 
                    :outputType="option.outputType" 
                    :info="true" 
                    :full="option.full" 
                    :fixedNumber="option.fixedNumber"
                    :fixed="option.fixed"
                    :canMove="option.canMove" 
                    :canMoveBox="option.canMoveBox" 
                    :original="option.original" 
                    :centerBox="option.centerBox" 
                    :autoCrop="option.autoCrop" 
                    :autoCropWidth="option.autoCropWidth" 
                    :autoCropHeight="option.autoCropHeight" 
                    :fixedBox="option.fixedBox"
                    @imgLoad="imgLoad"> 
                </vue-cropper>
            </div>
        </div>
        <!-- 工作场景图放大 -->
        <div class="big-img-business-popup" v-show="bigImgBusinessPopup" @click="closeBigImgBusinessPopup">
            <div class="big-img">
                <img :src="postModel.imgBusinessLicence" alt="" />
            </div>
        </div>
    </div>
</template>

<script>
import Util from '@/js/mUtils';
import { Picker,Notify, Toast  } from 'vant';
import {DLDict,DLArea,DLPType,DLIndustry} from '@/js/dl-code';
import { VueCropper } from "vue-cropper";
import OSS from 'ali-oss';
export default {
