            changeJobTypeValTwo: '',
            changeJobTypeValThree: '',
            firstTypeValue: '',
            secondTypeValue: '',
            thirdTypeValue: '',
            //岗位类型end
            // 招聘类型start
            checkboxGroup: ['月结'],                //2018-10-18增加的结算标准
            checkboxGroupCancel: [],           
            recruitDefault: '社会工',               //招聘类型
            settleValList: [],
