            recruitDemandTypeModel: 1,                           //招聘类型，传给后端的值
            // 招聘类型end
            postRiLowWages: '',                     //原日/月最小值字段
            postRiHighWages: '',                    //原日/月最大值字段
            settleStandardTimesUnit: false,          //结算标准单位元/小时
            
            sexPopup: false,                    //性别要求弹窗
            changeGender: '',                   //性别要求传给后端
            genderVal: '请选择性别要求',
            positionSexVal: '',
            genderList: ["不限","男","女"],
            sexListShow: [],
            workContentVal: '请填写工作内容',
            jobContent: '',                     //工作内容
            mongolia: false,
            //学历要求start
            educationList: [                   
                "不限","小学","初中","中专", "高中","大专","本科","硕士","博士"
            ],    
            educationListShow: [],              //信息回显的
            positionEducatVal: '',
            educationPopup: false,
            changeEducationMin: '',             //学历要求传给后端
