                fixedBox: true,
                downImg: "#"
            },
            imgValue:'',  // 裁剪图片的value
            oss:{
                region: '',
                accessKeyId: '',
                securityToken: '',
                accessKeySecret: '',
                bucket: ''
            }, 
            systemUserId: '',               //渠道商id
            bigImgBusinessPopup: false,     //工作场景图-放大弹窗
        }
    },
    mounted() {
        window.scrollTo(0, 0);
        this.workContentVal = '请填写工作内容'      //工作内容置空
        this.jobContent = ''                        //工作内容置空
        this.workContentValTextarea = ''            //工作内容置空

        this.educationListShow = DLDict.educationRequire
        this.postModel.consultTel = localStorage.getItem('qdsMobile');
        this.sexListShow = DLDict.genderRequire
        if(this.$route.query.positionId) {
            this.positionId = this.$route.query.positionId;                 //职位id
        }
        this.getPositionDate()                  //岗位类型
        this.getOssParams()                     //获取oss相关数据
        this.getCityDate()                      //城市数据
        this.getCustomerData()                  //获取关联客户
        this.eachPositionDetail()               //职位管理-编辑职位-职位详情
    },
    components: {
        Picker,
        Notify,
        Toast,
        VueCropper 
    },
    methods: {
        getOssParams() {                            // 图片信息 获取OSS参数
            let that = this;
            let token = Util.getCookie("qdsMToken"); 
            that.systemUserId = localStorage.getItem('qdsSCorpUserId')                                  //渠道商id
            that.axios(that.ossGlobalApiUrl + 'oss/assumeRole?token=' + token, {
                method: 'post',
                params: { 
                    sysUserId: that.systemUserId,       // 用户id。
                }
            })
            .then((response)=>{
                that.oss.accessKeyId = response.data.data.credentials.accessKeyId
                that.oss.accessKeySecret = response.data.data.credentials.accessKeySecret
                that.oss.securityToken = response.data.data.credentials.securityToken

            }).catch((error)=>{
            })
        },
        eachPositionDetail() {                  //职位管理-编辑职位-职位详情
            if(this.positionId) {
                let token = Util.getCookie('qdsMToken');
                let that = this;
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/getById?token=' + token,{
                    method: 'post',
                    params: {
                        corpDemandId: that.positionId
                    }
                })
                .then(function (response) {  
                    if (response.data.code == 10000) {
                        that.relationCustomerId = response.data.data.corpId                                         //关联客户id
                        that.postModel.customerName = response.data.data.corpName                                   //关联客户id
                        that.postModel.recruitNum = response.data.data.recruitNum                   //招聘人数
                        if(response.data.data.coverImgUrl == 'empty'){                                              //工作场景图
                            that.postModel.imgBusinessLicence = ''
                            that.coverImgUrl = ''
                        }else {
                            that.postModel.imgBusinessLicence = response.data.data.coverImgUrl
                            that.coverImgUrl = response.data.data.coverImgUrl
                            that.toggleUploadBtn = false
                        }
                        //岗位类型
                        that.firstTypeValue = DLPType.getName(response.data.data.jobTypeCode1)    
                        that.secondTypeValue = DLPType.getName(response.data.data.jobTypeCode2)
                        that.thirdTypeValue = DLPType.getName(response.data.data.jobTypeCode3) 
                        that.jobTypeVal =  that.firstTypeValue + '/' + that.secondTypeValue + '/' + that.thirdTypeValue
                        that.changeJobTypeCodeOne = response.data.data.jobTypeCode1
                        that.changeJobTypeCodeTwo = response.data.data.jobTypeCode2
                        that.changeJobTypeCodeThree = response.data.data.jobTypeCode3
                        that.recruitDemandTypeModel = response.data.data.recruitDemandType          //岗位类型name传的值

                        if(Number(that.recruitDemandTypeModel) == 1) {                              //招聘类型
                            that.recruitDefault = '社会工'
                            that.settleStandardTimesUnit = false
                        }
                        if(Number(that.recruitDemandTypeModel) == 5) {
                            that.recruitDefault = '小时工'
                            that.settleStandardTimesUnit = true
                        }
                        if(Number(that.recruitDemandTypeModel) == 8) {
                            that.recruitDefault = '学生工'
                            that.settleStandardTimesUnit = false
                        }
                        that.settleValListStr = response.data.data.balanceType                      //结算标准name传的值

                        if(response.data.data.salaryFrom && response.data.data.salaryTo) {
                            that.postModel.postLowSalary = response.data.data.salaryFrom           //结算标准页面显示综合薪资元/月低
                            that.postModel.postHighSalary = response.data.data.salaryTo            //结算标准页面显示综合薪资元/月高
                        }else if(response.data.data.monthSalaryLow && response.data.data.monthSalaryUp) {
                            that.postModel.postLowSalary = response.data.data.monthSalaryLow        //结算标准页面显示综合薪资元/月低
                            that.postModel.postHighSalary = response.data.data.monthSalaryUp        //结算标准页面显示综合薪资元/月高
                        }
                        if(response.data.data.daysSalaryLow && response.data.data.daysSalaryUp) {
                            that.postRiLowWages = response.data.data.daysSalaryLow                      //结算标准页面显示元/日低
                            that.postRiHighWages = response.data.data.daysSalaryUp                      //结算标准页面显示元/日高
                        }
                        if(response.data.data.hourSalaryLow && response.data.data.hourSalaryUp) {
                            that.postModel.postTimesLowWages = response.data.data.hourSalaryLow        //结算标准页面显示元/小时低
                            that.postModel.postTimesHighWages = response.data.data.hourSalaryUp        //结算标准页面显示元/小时高
                        }

                        that.postModel.payArrangement = response.data.data.payArrangement     //发薪时间
                        
                        that.postModel.postLowAge = response.data.data.ageFrom               //年龄要求
                        that.postModel.postHighAge = response.data.data.ageTo
                        
                        that.changeEducationMin = response.data.data.educationMin
                        that.positionEducatVal = Number(response.data.data.educationMin)                //学历要求
                        that.educationValue = that.educationListShow[that.positionEducatVal]

                        that.changeGender = response.data.data.gender
                        that.positionSexVal = Number(response.data.data.gender)                         //性别要求
                        that.genderVal = that.sexListShow[that.positionSexVal]
                       
                        that.postModel.workTime = response.data.data.workTime                 //工作时间

                        that.workContentVal = response.data.data.jobContent               //工作内容
                        that.jobContent = response.data.data.jobContent               //工作内容
                        that.workContentValTextarea = response.data.data.jobContent               //工作内容

                        that.changeWorkProvinceCode = response.data.data.workProvinceCode
                        that.changeWorkCityCode = response.data.data.workCityCode
                        that.changeWorkDistCode = response.data.data.workDistCode
                        that.showCityAddress =  DLArea.getName(that.changeWorkProvinceCode) + '/' + DLArea.getName(that.changeWorkCityCode) + '/' + DLArea.getName(that.changeWorkDistCode) + '/' + response.data.data.workAddress          //工作地址
                        that.positionDetailsAddress = response.data.data.workAddress
                        that.keyword = DLArea.getName(that.changeWorkProvinceCode) + '/' + DLArea.getName(that.changeWorkCityCode) + '/' + DLArea.getName(that.changeWorkDistCode) + '/'

                        
                        // that.changeEducationMin = response.data.data.educationMin
                        
                        if(response.data.data.consultTel) {
                            that.postModel.consultTel = response.data.data.consultTel                 //咨询电话
                        }

                        // 福利标签
                        that.showChanceWelfareList = response.data.data.welfareLabel                  
                        that.chanceWelfareList = response.data.data.welfareLabel 
                        that.showChanceWelfareList.forEach((item) => { 
                            that.changeWelfareLabelArr.push(item.id)
                        })
                        
                        that.changeWelfareLabelStr = that.changeWelfareLabelArr.join(",");
                        

                    }else {

                    }
                })
                .catch(function (error) {        
                });
            }
        },
        backHistory() {
            window.history.back(-1)
        },
        hideWorkContentPopup() {
            this.workContentPopup = false
        },
        sureWorkContent() {                     //工作内容
            if(this.workContentValTextarea == '') {
                Notify({
                    message: '请输入工作内容',
                    duration: 1500,
                    background: '#FF6262'
                });
            }else {
                this.workContentVal = this.workContentValTextarea
                this.jobContent = this.workContentValTextarea
                this.workContentPopup = false
            }
            
        },
        getCustomerData() {                 //获取关联客户
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/getCorpInfoByChannelId?token=' + token,{
                method: 'post'
            })
            .then( response=> {
                if(response.data.code == 10000) {           //企业状态对应的含义  0：未发布、1：已发布、-1：已下架
                    if(response.data.data) {
                        that.customerArr = response.data.data;
                        that.authSuccess = that.customerArr.filter(item => item.state === 1)       //过滤得到认证通过的关联客户
                        that.authRelationCustomerList = that.authSuccess
                    }
                }else {
                    Notify({
                        message: '获取关联客户失败，请联系客服021-22876396'   + '(错误码: ' + response.data.code + ')',
                        duration: 1500,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        searchReleationCustomer(query) {        //关联客户搜索
            var that = this
            that.authRelationCustomerList = [];
            if (query !== '') {
                that.loading = true;
                setTimeout(() => {
                    that.loading = false;
                    that.authSuccess.forEach((item) => {
                        if(item.corpName.indexOf(query) != -1) {
                            that.authRelationCustomerList.push(item)
                        }
                    })
                }, 200);
            } else {
                that.authRelationCustomerList = [];
            }
        },
        chanceAuthCustomer(name,id) {          //关联客户
            this.relationCustomer = name
            this.relationCustomerId = id
        },
        chanceEducation() {                         //显示学历选择选择器
            this.mongolia = true
            this.educationPopup = true
        },
        changeEducation(picker,value,index) {      //教育经验选择和赋值
            this.changeEducationMin = index + 1;
            this.educationValue = value
        },
        cancelEduca() {                             //隐藏学历选择选择器
            this.changeEducationMin = ''
            this.educationValue = '请选择学历要求'
            this.mongolia = false
            this.educationPopup = false
        },
        confirmEducation() {                        //确认学历选择选择器
            if(this.changeEducationMin == '') {
                this.changeEducationMin = 1
                this.educationValue = '不限'
            }
            this.mongolia = false
            this.educationPopup = false
        },
        showGenderPopup() {                              //显示性别选择选择器
            this.mongolia = true
            this.sexPopup = true
        },
        changeSex(picker,value,index) {                          //性别选择
            this.changeGender = index + 1;
            this.genderVal = value
        },
        confirmGender() {                                //确认性别选择选择器
            if(this.changeGender == '') {
                this.changeGender = 1
                this.genderVal = '不限'
            }
            this.mongolia = false
            this.sexPopup = false
        },
        cancelGender() {                                     //隐藏性别选择选择器
            this.changeGender = ''
            this.genderVal = '请选择性别要求'
            this.mongolia = false
            this.sexPopup = false
        },
        // 岗位类型start
        getPositionDate() {                     //岗位类型三级联动数据
            var token = Util.getCookie("qdsMToken");  
            let that = this;
            that.axios(that.globalApiUrl + "api/recruitManager/findAllCodePost?token=" + token,{
                method: "get",
                params: {
                    type: 1
                }
            })
            .then(function(response) {
                if (response.data && response.data.length > 0) {
                    var _resArr = response.data;
                    _resArr.forEach((item, index) => {
                        //用item操作每一条数据。
                        var child = {
                            value: item.code,
                            label: item.name,
                            children:[]
                        };  
                        that.positionTypeArr.push(child);
                        var _resArr2 = _resArr[index].childrenList;
                        _resArr2.forEach((item2, index2) => {
                            //用item操作每一条数据。
                            var child2 = {
                                value: item2.code,
                                label: item2.name,
                                children:[]
                            };  
                            that.positionTypeArr[index].children.push(child2);
                                var _resArr3 = _resArr2[index2].childrenList;
                                _resArr3.forEach((item3, index3) => {
                            //用item操作每一条数据。
                            var child3 = {
                                value: item3.code,
                                label: item3.name,
                            }; 
                            that.positionTypeArr[index].children[index2].children.push(child3);
                            });
                        });
                    });
                }
            })
            .catch(function(error) {
            });
        },
        getCityDate() { // 城市 数据
            let token = Util.getCookie("qdsMToken");
            let that = this;
            that.axios(that.globalApiUrl + "api/recruitManager/findAllCity?token=" + token,{
                method: "get",
                params: {
                    type:1,
                }
            })
            .then(function(response) {
                if (response.data && response.data.length > 0) {
                    var _resArr = response.data;
                        _resArr.forEach((item, index) => {
                        //用item操作每一条数据。
                        var child = {
                            value: item.code,
                            label: item.name,
                            children:[]
                        };  
                        that.cityArr.push(child);
                        var _resArr2 = _resArr[index].childrenList;
                            _resArr2.forEach((item2, index2) => {
                        //用item操作每一条数据。
                        var child2 = {
                            value: item2.code,
                            label: item2.name,
                            children:[]
                        };  
                        that.cityArr[index].children.push(child2);
                            var _resArr3 = _resArr2[index2].childrenList;
                            _resArr3.forEach((item3, index3) => {
                        //用item操作每一条数据。
                        var child3 = {
                            value: item3.code,
                            label: item3.name,
                        }; 
                        that.cityArr[index].children[index2].children.push(child3);
                        
                    });
                        
                    });
                        
                    });
                }
            })
            .catch(function(error) {
            });
        },
        chancePostType() {                  //展示岗位类型三级联动
            this.mongolia = true
            this.postTypePopup = true
        },
        postTypeCancel() {                  //隐藏岗位类型三级联动
            this.mongolia = false
            this.postTypePopup = false
        },
        postTypeSelect(select,value,label) {              //岗位类型一级
            this.selectCode = value
            this.changeJobTypeCodeTimeOne = value
            this.changeJobTypeValOne = label
            this.positionTypeArrTwo = []
            this.positionTypeArrThree = []
            if(select.length > 0){
                this.positionTypeArrTwo = select
            }
        },
        postTypeSelectTwo(select,value,label) {           //岗位类型二级
            this.selectTwoCode = value
            this.changeJobTypeCodeTimeTwo = value
            this.changeJobTypeValTwo = label
            this.positionTypeArrThree = []
            if(select.length > 0){
                this.positionTypeArrThree = select
            }
        },
        postTypeSelectThree(label,value) {          //岗位类型三级
            this.selectthreeCode = value
            this.changeJobTypeCodeTimeThree = value
            this.changeJobTypeValThree = label
            var that = this;
            var token = Util.getCookie("qdsMToken");                                
            that.axios(that.qdsGlobalApiUrl  + 'channelDemand/getDemandTempletInfoByCode?token=' + token, {
                method: 'post',
                params: { 
                    code: value,   
                    level: 3,    
                }
            })
            .then((response)=>{
                if(response.data.data) {
                    that.workContentVal = response.data.data.workDesc                       //工作内容
                    that.jobContent = response.data.data.workDesc                           //工作内容
                    that.workContentValTextarea = response.data.data.workDesc
                    that.postModel.postLowSalary = response.data.data.salaryFrom                   //结算标准页面显示综合薪资元/月低
                    that.postModel.postHighSalary = response.data.data.salaryTo                    //结算标准页面显示综合薪资元/月高
                }else {
                    that.workContentVal = '请填写工作内容'                       //工作内容
                    that.jobContent = ''                                //工作内容
                    that.workContentValTextarea = ''
                    that.postModel.postLowSalary = ''                   //结算标准页面显示综合薪资元/月低
                    that.postModel.postHighSalary = ''                    //结算标准页面显示综合薪资元/月高
                }
            }).catch((error)=>{

            }) 
        },
        postTypeSure() {                    //确认岗位类型
            this.changeJobTypeCodeOne = this.changeJobTypeCodeTimeOne
            this.changeJobTypeCodeTwo = this.changeJobTypeCodeTimeTwo
            this.changeJobTypeCodeThree = this.changeJobTypeCodeTimeThree
            this.jobTypeVal = this.changeJobTypeValOne + '/' + this.changeJobTypeValTwo + '/' + this.changeJobTypeValThree
            this.mongolia = false
            this.postTypePopup = false
        },
        // 岗位类型end
        // 福利标签开始
        showWelfarePopup() {                                    //显示福利标签
            var that = this;
            var token = Util.getCookie("qdsMToken");  
            that.mongolia = true
            that.welfarePopup = true
            that.showWelfareList = []
            that.showChanceWelfareList.forEach((item) => {
                that.showWelfareList.push(item.name)
            })
            that.welfareStorage = [];
            that.welfareStorage =[].concat(that.chanceWelfareList) ;      // 将选中的标签保存两份  如果点击保存 那么存储chanceWelfareList   如果点击取消, 就存储welfareStorage

            if(that.gerWelfareStateList.length == 0) {
                that.axios(that.globalApiUrl + 'api/recruitManager/findListByCodeType?token=' + token ,{
                    method: 'post',
                    params: {
                        typeCode: 'welfareLable'
                    },
                    timeout: 10000
                })
                .then(function (response) {
                    that.gerWelfareStateList = response.data
                    response.data.forEach((item) =>{
                        if(item.code == 'corporateWelfare') {
                            if(item.childDictList.length == 0 || item.childDictList == null || item.childDictList == undefined) {
                                that.companyWelfareList.push({
                                    id: item.id,
                                    name: item.name
                                })
                            }else if(item.childDictList.length > 0) {
                                item.childDictList.forEach((itemItem) =>{
                                    if(itemItem.childDictList.length == 0 || itemItem.childDictList == null || itemItem.childDictList == undefined) {
                                        that.companyWelfareList.push({
                                            id: itemItem.id,
                                            name: itemItem.name
                                        })
                                    }
                                })
                            }
                        }
                        if(item.code == 'businessWelfare') {
                            if(item.childDictList.length == 0 || item.childDictList == null || item.childDictList == undefined) {
                                that.jobWelfareList.push({
                                    id: item.id,
                                    name: item.name
                                })
                            }else if(item.childDictList.length > 0) {
                                item.childDictList.forEach((itemItem) =>{
                                    if(itemItem.childDictList.length == 0 || itemItem.childDictList == null || itemItem.childDictList == undefined) {
                                        that.jobWelfareList.push({
                                            id: itemItem.id,
                                            name: itemItem.name
                                        })
                                    }else if(itemItem.childDictList.length > 0) {
                                        itemItem.childDictList.forEach((itemItemItem) =>{
                                            if(itemItemItem.childDictList.length == 0 || itemItemItem.childDictList == null || itemItemItem.childDictList == undefined) {
                                                that.jobWelfareList.push({
                                                    id: itemItemItem.id,
                                                    name: itemItemItem.name
                                                })
                                            }
                                        })
                                    }
                                })
                            }
                        }
                    })
                })
                .catch(function (error) {
                    Toast('系统异常，请重新登录或者联系客服021-22876396')
                });
            }
        },
        chanceWelfare(item,id,e) {           //选择福利标签 
            if(e) {
                this.chanceWelfareList.push({
                    id: id,
                    name: item.name
                })
            }else {
                for(var i=0; i<this.chanceWelfareList.length; i++) {
                    if(this.chanceWelfareList[i].name == item.name) {
                        this.chanceWelfareList.splice(i, 1)
                    }
                }
            }
        },
        welfareCancel() {
            this.mongolia = false
            this.welfarePopup = false
        },
        preservationWelfare() {                 //福利标签-提交选中的
            var that = this
            that.preservationWelfareState = true
            if(that.preservationWelfareState) {
                that.welfarfDialogVisible =! that.welfarfDialogVisible
                that.showChanceWelfareList = that.chanceWelfareList
            }
            that.changeWelfareLabelStr = '';
            that.changeWelfareLabelArr = [] ;
            that.showChanceWelfareList.forEach((item) => {
                that.changeWelfareLabelArr.push(item.id)
            })
            that.changeWelfareLabelStr = that.changeWelfareLabelArr.join(",");
            this.mongolia = false
            this.welfarePopup = false
            
        },
        qdsWorkContent() {
            this.workContentPopup = true
        },
        // 招聘类型
        recruitFunc(recruitVal) {                         //招聘类型
            if(recruitVal == '社会工') {
                this.recruitDemandTypeModel = 1                   //招聘类型传给后端的值
                this.checkboxGroup = this.checkboxGroupCancel       //赋空数组
                this.checkboxGroup = ['月结']                       //结算标准展示的值
                this.settleValListArr = [1]                         //结算标准传给后端的值
                this.settleValListStr = '1'
                this.settleStandardTimesUnit = false                //小时
            }
            if(recruitVal == '学生工') {                
                this.recruitDemandTypeModel = 8                   //招聘类型传给后端的值
                this.checkboxGroup = this.checkboxGroupCancel       //赋空数组
                this.checkboxGroup = ['月结']                       //结算标准展示的值
                this.settleValListArr = [1]                          //结算标准传给后端的值
                this.settleValListStr = '1'
                this.settleStandardTimesUnit = false                //小时
            }
            if(recruitVal == '小时工') {
                this.recruitDemandTypeModel = 5                  //招聘类型传给后端的值
                this.checkboxGroup = this.checkboxGroupCancel       //赋空数组
                this.checkboxGroup = ['月结']                       //结算标准展示的值
                this.settleValListArr = [1,3]                          //结算标准传给后端的值
                this.settleValListStr = '1,3'
                this.settleStandardTimesUnit = true             //小时
            }
        },
        // 百度地图开始
        closeBdMapDialog() {            //关闭百度地图弹窗
            this.mongolia = false
            this.centerDialogVisible = false
        },
        showBaiduMapPopup() {
            this.mongolia = true
            this.centerDialogVisible = true
        },
        changeCity(e) {                 //百度地图-城市
            var that = this;
            that.changeWorkProvinceCode = e[0];
            that.changeWorkCityCode = e[1];
            that.changeWorkDistCode =  e[2];
            that.cityArr.forEach((item) => {
                if(item.value == e[0]){                 //获取省code对应的省中文
                    that.provinceValue = item.label
                    item.children.forEach((cityItem) => {       
                        if(cityItem.value == e[1]) {       //获取市code对应的市中文
                            that.cityValue = cityItem.label
                            cityItem.children.forEach((distItem) => {   
                                if(distItem.value == e[2]) {       //获取区code对应的区中文
                                    that.distValue = distItem.label
                                }
                            })
                        }
                    })
                }
            })
            that.keyword = that.provinceValue + "/" + that.cityValue + "/" + that.distValue + "/" 
            that.showCityAddress = that.keyword + that.addressDetails
        },
        addressDetailsBlur(){                     //详细地址输入框动态改变
            this.showCityAddress = this.keyword + String(this.addressDetails)
            this.positionDetailsAddress = this.addressDetails
        },
        preservationAddress() {                 //选取地图地址弹窗保存
            if(this.keyword == "") {
                Notify({
                    message: '请选择省市区',
                    duration: 1500,
                    background: '#FF6262'
                });
            }else if(this.changeWorkProvinceCode == undefined && this.changeWorkCityCode == undefined || this.changeWorkDistCode == undefined) {
                Notify({
                    message: '请选择完善的省市区',
                    duration: 1500,
                    background: '#FF6262'
                });
            }else if(String(this.addressDetails) == ""){
                Notify({
                    message: '请填写详细地址',
                    duration: 1500,
                    background: '#FF6262'
                });
            }else{
                this.mongolia = false
                this.centerDialogVisible = false
            }
        },
        // 百度地图结束
        // 图片裁剪开始
        uploadImg (e, num) {        //点击本地图片   
            this.imgValue = "";     
            var file = e.target.files[0]   
            this.filecCoverImgType = file.name.split('.')[1]
            if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {                
                alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')                
                    return false            
                };            
                var reader = new FileReader()            
                reader.onload = (e) => {                
                    let data                
                    if (typeof e.target.result === 'object') { // 把Array Buffer转化为blob 如果是base64不需要                    
                    data = window.URL.createObjectURL(new Blob([e.target.result]))                
                    } else {                    
                        data = e.target.result                
                    }                
                    if (num === 1) {                    
                        this.option.img = data                
                    }     
                    this.mongolia = true;
                    this.showCutImg = true;        
                } // 转化为base64            
                reader.readAsDataURL(file) // 转化为blob reader.readAsArrayBuffer(file)        
        }, 
        imgLoad (msg) {     // 显示本地图片是否成功
            console.log(msg)
        },
        trueCutImg (type) {    // 确定裁剪
            var that = this;
            if(type === "blob") {
                that.$refs.cropper.getCropBlob((data) => {                 
                    that.downImg = window.URL.createObjectURL(data)       
                })
            }else {
                that.$refs.cropper.getCropData((data) =>{
                    if (data.length > 0) {  //判断是使用原图还是裁剪图
                        var arr = data.split(','),     //方法用于把cropImage分割成字符串数组。
                            mime = arr[0].match(/:(.*?);/)[1],
                            bstr = atob(arr[1]),            // 去掉url的头，并转化为byte(解码)
                            n = bstr.length,                // 处理异常,将ascii码小于0的转换为大于0
                            u8arr = new Uint8Array(n);      // 生成视图（直接针对内存）：8 位无符号整数值的类型化数组，长度1个字节
                        while(n--){
                            u8arr[n] = bstr.charCodeAt(n);    //方法可返回指定位置的字符的 Unicode 编码
                        }
                    }
                    var obj = new Blob([u8arr], {type:mime}); 
                    var OSS = require('ali-oss');
                    var client = new OSS({
                        region : "oss-cn-hangzhou",
                        accessKeyId: that.oss.accessKeyId,
                        accessKeySecret: that.oss.accessKeySecret,
                        stsToken: that.oss.securityToken,
                        bucket: "ylsite"
                    });
                    var date = new Date();
                    var toLocaleDateString = date.toLocaleDateString()
                    var timestamp = Date.parse(new Date()); 
                    async function multipartUpload() {
                        that.loading = true
                        try {
                            let result = await client.multipartUpload('ylkz/job' + '/' + toLocaleDateString + '/' + that.systemUserId + '_' + timestamp + '.' + that.filecCoverImgType, obj);
                            if(result.res.requestUrls[0].indexOf('?') !== -1){
                                var ossArr = result.res.requestUrls[0].split('?')
                                that.postModel.imgBusinessLicence = ossArr[0]
                                that.coverImgUrl = ossArr[0]
                                that.toggleUploadBtn = false
                                that.loading = false
                            }else{
                                that.postModel.imgBusinessLicence = result.res.requestUrls[0]
                                that.coverImgUrl = result.res.requestUrls[0]
                                that.toggleUploadBtn = false
                                that.loading = false
                            }
                        }catch(e) {
                            // 捕获超时异常
                            that.loading = false
                            that.getOssParams()
                            if(e.code === 'ConnectionTimeoutError') {
                                console.log("Woops,超时啦!");
                            }
                        }
                        that.loading = false
                    }
                    multipartUpload()
                })
            }
            that.mongolia = false
            that.showCutImg = false;
        },
        clearImg() {            //删除工作场景图
            this.toggleUploadBtn = true
            this.postModel.imgBusinessLicence = ""
            this.coverImgUrl = ""
        },
        falseCutImg() {
            this.mongolia = false
            this.showCutImg = false;
        },
        // 图片裁剪结束
        bigImgBusinessLicence() {               //放大工作场景图
            this.bigImgBusinessPopup = true
            this.mongolia = true
        },
        closeBigImgBusinessPopup() {                //关闭放大工作场景图弹窗
            this.bigImgBusinessPopup = false
            this.mongolia = false
        },
        // 保存接口
        savePostForm() {
            var that = this
            var token = Util.getCookie('qdsMToken');
            if(that.postModel.customerName.length == 0) {
                Notify({
                    message: '请填写关联客户',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.recruitNum.length == 0) {
                Notify({
                    message: '请填写招聘人数',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.recruitNum))) {
                Notify({
                    message: '招聘人数只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.recruitNum > 5000 ) {
                Notify({
                    message: '招聘人数不可大于5000人',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeJobTypeCodeThree.length == 0 ) {
                Notify({
                    message: '请选择岗位类型',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            } 
            if(that.postModel.postLowSalary.length == 0 ) {
                Notify({
                    message: '请输入最低工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postLowSalary))) {
                Notify({
                    message: '最低工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighSalary.length == 0 ) {
                Notify({
                    message: '请输入最高工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postHighSalary))) {
                Notify({
                    message: '最高工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(Number(that.postModel.postLowSalary) > Number(that.postModel.postHighSalary)) {
                Notify({
                    message: '最高薪资必须大于最低薪资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if((Number(that.postModel.postHighSalary)) / (Number(that.postModel.postLowSalary)) > 2) {
                Notify({
                    message: '最高薪资不可大于最低薪资两倍',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.settleStandardTimesUnit == true && that.postModel.postTimesLowWages.length == 0) {
                Notify({
                    message: '请输入小时工价最低工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && !(/^[+]{0,1}(\d+)$/.test(that.postModel.postTimesLowWages))) {
                Notify({
                    message: '小时工价最低工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && that.postModel.postTimesHighWages.length == 0 ) {
                Notify({
                    message: '请输入小时工价最高工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && !(/^[+]{0,1}(\d+)$/.test(that.postModel.postTimesHighWages))) {
                Notify({
                    message: '小时工价最高工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && (Number(that.postModel.postTimesLowWages) > Number(that.postModel.postTimesHighWages))) {
                Notify({
                    message: '小时工价最高薪资必须大于最低薪资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && ((Number(that.postModel.postTimesHighWages)) / (Number(that.postModel.postTimesLowWages)) > 2)) {
                Notify({
                    message: '小时工价最高薪资不可大于最低薪资两倍',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.payArrangement.length == 0 ) {
                Notify({
                    message: '请输入发薪时间',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeWelfareLabelStr.length == 0 ) {
                Notify({
                    message: '请选择福利标签',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.postLowAge.length == 0 ) {
                Notify({
                    message: '请输入最低年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postLowAge))) {
                Notify({
                    message: '最低年龄只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postLowAge < 16) {
                Notify({
                    message: '年龄最小不可小于16岁',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighAge.length == 0 ) {
                Notify({
                    message: '请输入最高年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postHighAge))) {
                Notify({
                    message: '最高年龄只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighAge > 70 ) {
                Notify({
                    message: '年龄最大不可大于70岁',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if((Number(that.postModel.postLowAge)) > (Number(that.postModel.postHighAge))) {
                Notify({
                    message: '最大年龄必须大于最小年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeEducationMin.length == 0) {
                Notify({
                    message: '请选择学历',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeGender.length == 0) {
                Notify({
                    message: '请选择性别',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.workTime.length == 0 ) {
                Notify({
                    message: '请输入工作时间',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.positionDetailsAddress.length == 0) {
                Notify({
                    message: '请选择工作地址',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.consultTel == '' || that.postModel.consultTel.length == 0) {
                Notify({
                    message: '请填写咨询电话',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '1' && !(/(^1[0-9]{10}$)/.test(that.postModel.consultTel))) {
                Notify({
                    message: '请填写正确的手机号',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '0' && !(/(^[0-9]*$)/.test(that.postModel.consultTel))) {
                Notify({
                    message: '咨询电话必须为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '0' && (that.postModel.consultTel.length != 11 && that.postModel.consultTel.length != 12)){
                Notify({
                    message: '0开头的座机号位数必须为11位或者12位',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.workContentVal == '请填写工作内容') {
                Notify({
                    message: '请填写工作内容',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.coverImgUrl == '') {
                Notify({
                    message: '请上传工作场景图',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.positionId) {                               //已存在职位-编辑
                that.loading = true
                let editForm = document.getElementById('addPosition');
                let editData = new FormData(editForm);
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateDemand?operationType=' + 1 + "&" + "token=" + token,{
                    method: 'post',
                    data: editData,
                    timeout: 10000
                })
                .then(function(response){
                    if (response.data.code == 10000) {
                        that.loading = false
                        that.$router.push('/qdsPostList?postHeadTab=' + 1)
                    }else{
                        that.loading = false 
                        Toast(response.data.message + '，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')')
                    }
                })
                .catch(function(error){
                    that.loading = false 
                })
            }else {                                     //新添加职位-保存
                that.loading = true   
                let addForm = document.getElementById('addPosition');
                let addData = new FormData(addForm);
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/releaseDemand?operationType=' + 1 + "&" + "token=" + token,{
                    method: 'post',
                    data: addData,
                    timeout: 10000
                })
                .then(function(response){
                    if (response.data.code == 10000) {
                        that.loading = false 
                        that.$router.push('/qdsPostList?postHeadTab=' + 1)
                    }else{
                        that.loading = false 
                        Toast(response.data.message + '，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')')
                    }
                })
                .catch(function(error){
                    that.loading = false 
                    Toast('保存失败，联系客服021-22876396')
                })
            } 
            
        },
