            // }else if(that.postModel.consultTel.charAt(0) == '1' && !(/(^1[0-9]{10}$)/.test(that.postModel.consultTel))) {
            //     Notify({
            //         message: '请填写正确的手机号',
            //         duration: 1500,
            //         background: '#FF6262'
            //     });
            // }else if(that.postModel.consultTel.charAt(0) == '0' && !(/(^[0-9]*$)/.test(that.postModel.consultTel))) {
            //     Notify({
            //         message: '咨询电话必须为数字',
            //         duration: 1500,
            //         background: '#FF6262'
            //     });
            // }else if(that.postModel.consultTel.charAt(0) == '0' && (that.postModel.consultTel.length != 11 && that.postModel.consultTel.length != 12)){
            //     Notify({
            //         message: '0开头的座机号位数必须为11位或者12位',
            //         duration: 1500,
            //         background: '#FF6262'
            //     });
            // }else if(that.workContentVal == '请填写工作内容') {
            //     Notify({
            //         message: '请填写工作内容',
            //         duration: 1500,
            //         background: '#FF6262'
            //     });
            // }else if(that.coverImgUrl == '') {
            //     Notify({
            //         message: '请上传工作场景图',
            //         duration: 1500,
            //         background: '#FF6262'
            //     });
            // }
            if(that.postModel.customerName.length == 0) {
                Notify({
                    message: '请填写关联客户',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.recruitNum.length == 0) {
                Notify({
                    message: '请填写招聘人数',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.recruitNum))) {
                Notify({
                    message: '招聘人数只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.recruitNum > 5000 ) {
                Notify({
                    message: '招聘人数不可大于5000人',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeJobTypeCodeThree.length == 0 ) {
                Notify({
                    message: '请选择岗位类型',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            } 
            if(that.postModel.postLowSalary.length == 0 ) {
                Notify({
                    message: '请输入最低工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postLowSalary))) {
                Notify({
                    message: '最低工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighSalary.length == 0 ) {
                Notify({
                    message: '请输入最高工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postHighSalary))) {
                Notify({
                    message: '最高工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(Number(that.postModel.postLowSalary) > Number(that.postModel.postHighSalary)) {
                Notify({
                    message: '最高薪资必须大于最低薪资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if((Number(that.postModel.postHighSalary)) / (Number(that.postModel.postLowSalary)) > 2) {
                Notify({
                    message: '最高薪资不可大于最低薪资两倍',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.settleStandardTimesUnit == true && that.postModel.postTimesLowWages.length == 0) {
                Notify({
                    message: '请输入小时工价最低工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && !(/^[+]{0,1}(\d+)$/.test(that.postModel.postTimesLowWages))) {
                Notify({
                    message: '小时工价最低工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && that.postModel.postTimesHighWages.length == 0 ) {
                Notify({
                    message: '请输入小时工价最高工资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && !(/^[+]{0,1}(\d+)$/.test(that.postModel.postTimesHighWages))) {
                Notify({
                    message: '小时工价最高工资只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && (Number(that.postModel.postTimesLowWages) > Number(that.postModel.postTimesHighWages))) {
                Notify({
                    message: '小时工价最高薪资必须大于最低薪资',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.settleStandardTimesUnit == true && ((Number(that.postModel.postTimesHighWages)) / (Number(that.postModel.postTimesLowWages)) > 2)) {
                Notify({
                    message: '小时工价最高薪资不可大于最低薪资两倍',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.payArrangement.length == 0 ) {
                Notify({
                    message: '请输入发薪时间',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeWelfareLabelStr.length == 0 ) {
                Notify({
                    message: '请选择福利标签',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.postLowAge.length == 0 ) {
                Notify({
                    message: '请输入最低年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postLowAge))) {
                Notify({
                    message: '最低年龄只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postLowAge < 16) {
                Notify({
                    message: '年龄最小不可小于16岁',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighAge.length == 0 ) {
                Notify({
                    message: '请输入最高年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(!(/^[+]{0,1}(\d+)$/.test(that.postModel.postHighAge))) {
                Notify({
                    message: '最高年龄只能为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.postHighAge > 70 ) {
                Notify({
                    message: '年龄最大不可大于70岁',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if((Number(that.postModel.postLowAge)) > (Number(that.postModel.postHighAge))) {
                Notify({
                    message: '最大年龄必须大于最小年龄',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeEducationMin.length == 0) {
                Notify({
                    message: '请选择学历',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.changeGender.length == 0) {
                Notify({
                    message: '请选择性别',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.workTime.length == 0 ) {
                Notify({
                    message: '请输入工作时间',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.positionDetailsAddress.length == 0) {
                Notify({
                    message: '请选择工作地址',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.postModel.consultTel == '' || that.postModel.consultTel.length == 0) {
                Notify({
                    message: '请填写咨询电话',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '1' && !(/(^1[0-9]{10}$)/.test(that.postModel.consultTel))) {
                Notify({
                    message: '请填写正确的手机号',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '0' && !(/(^[0-9]*$)/.test(that.postModel.consultTel))) {
                Notify({
                    message: '咨询电话必须为数字',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }else if(that.postModel.consultTel.charAt(0) == '0' && (that.postModel.consultTel.length != 11 && that.postModel.consultTel.length != 12)){
                Notify({
                    message: '0开头的座机号位数必须为11位或者12位',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.workContentVal == '请填写工作内容') {
                Notify({
                    message: '请填写工作内容',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.coverImgUrl == '') {
                Notify({
                    message: '请上传工作场景图',
                    duration: 1500,
                    background: '#FF6262'
                });
                return false
            }
            if(that.positionId) {                               //已存在职位-编辑
                that.loading = true
                let editForm = document.getElementById('addPosition');
                let editData = new FormData(editForm);
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateDemand?operationType=' + 2 + "&" + "token=" + token,{
                    method: 'post',
                    data: editData,
                    timeout: 10000
                })
                .then(function(response){
                    if (response.data.code == 10000) {
                        that.loading = false
                        that.$router.push('/qdsPostList?postHeadTab=' + 2)
                    }else{
                        that.loading = false 
                        Toast(response.data.message + '，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')')
                    }
                })
                .catch(function(error){
                    that.loading = false 
                })
            }else {                                     //新添加职位-保存
                that.loading = true   
                let addForm = document.getElementById('addPosition');
                let addData = new FormData(addForm);
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/releaseDemand?operationType=' + 2 + "&" + "token=" + token,{
                    method: 'post',
                    data: addData,
                    timeout: 10000
                })
                .then(function(response){
                    if (response.data.code == 10000) {
                        that.loading = false 
                        that.$router.push('/qdsPostList?postHeadTab=' + 2)
                    }else{
                        that.loading = false 
                        Toast(response.data.message + '，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')')
                    }
                })
                .catch(function(error){
                    that.loading = false 
                    Toast('保存并上架失败，联系客服021-22876396')
                })
            } 
        },
    }
}
</script>

